@import url('https://fonts.googleapis.com/css2?family=Kaisei+Tokumin:wght@400;500;700&family=Poppins:wght@300;400;500&display=swap');

:root {
  --lg-font: 'Kaisei Tokumin', serif;
  --sm-font: 'Poppins', sans-serif;
  --pink: #e5345b;
  --pinkdanger: #ff7b00;
}

body {
  font-family: var(--sm-font);
}

#changeText {
  color: var(--pinkdanger);
}

/* button */
.bg-primary {
  background-color: var(--pink) !important;
}

/* button */
.bg-primary {
  background-color: var(--pink) !important;
}

.btn:not(.nav-btns button) {
  background-color: #fff;
  color: rgb(85, 85, 85);
  padding: 10px 28px;
  border-radius: 25px;
  border: 1px solid rgb(85, 85, 85);
}

.btn:not(.nav-btns button):hover {
  background-color: var(--pink);
  color: #fff;
  border-color: var(--pink);
}

/* text color */
.text-danger {
  color: var(--pinkdanger) !important;
}

/* navbar */

.navbar {
  box-shadow: 03px 9px 3px rgba(0, 0, 0, 0.1);
}

.navbar-brand img {
  width: 30px;
}

.navbar-brand span {
  letter-spacing: 2px;
  font-family: var(--lg-font);
}

.nav-link:hover {
  color: var(--pink) !important;
}

.nav-item {
  border-bottom: 0.5px solid rgba(0, 0, 0, 0.05);
}

/* header */

#header {
  background: url(../../../images/banner-img-1.jpg) top/cover no-repeat;
}

.carousel-inner h1 {
  font-size: 60px;
  font-family: var(--lg-font);
}

.carousel-item .btn {
  border-color: #fff !important;
}

.carousel-item .btn:hover {
  border-color: var(--pink) !important;

}

/* title */
.title h2::before {
  position: absolute;
  content: "";
  width: 4px;
  height: 50px;
  background-color: var(--pink);
  left: -20px;
  top: 50%;
  transform: translateY(-50%);
}

/* collection */
.active-filter-btn {
  background-color: var(--pink) !important;
  color: #fff !important;
  border-color: var(--pink) !important;
}

.filter-button-group .btn:hover {
  color: #fff !important;
}

.collection-img span {
  top: 20px;
  right: 20px;
  width: 46px;
  height: 46px;
  border-radius: 50%;
}

/* special */
.special-img span {
  top: 20px;
  right: 20px;
}

.special-list .btn {
  padding: 8px 20px !important;
}


.special-img img {
  transition: all 0.3s ease;
}

.special-img:hover img {
  transform: scale(1.2);
}

/* offers */
#offers {
  background: url(../../../images/offer_img.jpg) center/cover no-repeat;
}

#offers .row {
  min-height: 60vh;
}

.offers-content span {
  font-size: 28px;
}

.offers-content h2 {
  font-size: 60px;
  font-family: var(--lg-font);
}

.offers-content .btn {
  border-color: transparent !important;
}

/* about us */
#about {
  background-color: rgba(0, 0, 0, 0.05);
}

/* media queries */

@media(min-width: 992px) {
  .nav-item {
    border-bottom: none;
  }
}